import { Activity } from "./Activity";
import { BpmnModel } from "./BpmnModel";
import { ServiceTask } from "./ServiceTask";

export class MergeActivity extends ServiceTask {
  static Clr_Type_ID: string = "bpmn.MergeActivity";
  ClrTypeID: string = MergeActivity.Clr_Type_ID;
  private static MEXTAG: string = 'bpmn.MergeActivity';



  constructor(model: BpmnModel) {
    super(model);
  }

  ResetBeforeLoadFrom() {
    super.ResetBeforeLoadFrom();
  }

  ConvertToJsonObject(): any {
    let obj = super.ConvertToJsonObject();

    return obj;
  }

  LoadFromJsonObject(obj: any) {
    super.LoadFromJsonObject(obj);

  }

  NewInstance(): MergeActivity {
    return new MergeActivity(this.Model);
  }

  clone(): MergeActivity {
    const c = super.clone() as MergeActivity;
    c.Name = this.Name;
    c.NameLanguage = this.NameLanguage && {
      ...this.NameLanguage,
    };
    return c;
  }
}
